export function formatDate(dateString) {
  const date = new Date(dateString);
  let dd = date.getDate() + 1;
  let mm = date.getMonth() + 1;

  const yyyy = date.getFullYear();
  if (dd < 10) {
    dd = '0' + dd;
  }
  if (mm < 10) {
    mm = '0' + mm;
  }
  return `${mm}/${dd}/${yyyy}`;
}

export default function timeAgo(date) {
  const NOW = new Date();
  const times = [['second', 1], ['minute', 60], ['hour', 3600], ['day', 86400], ['week', 604800], ['month', 2592000], ['year', 31536000]];
  var diff = Math.round((NOW - date) / 1000);
  for (var t = 0; t < times.length; t++) {
    if (diff < times[t][1]) {
      if (t == 0) {
        return 'Just now';
      } else {
        diff = Math.round(diff / times[t - 1][1]);
        return diff + ' ' + times[t - 1][0] + (diff == 1 ? ' ago' : 's ago');
      }
    }
  }
}

export function formatTimeAgo(timeValue, includeMinutes = false) {
  timeValue = new Date(timeValue);
  const weeks = Math.floor(timeValue / (60 * 24 * 7));
  const days = Math.floor((timeValue - weeks * 60 * 24 * 7) / (60 * 24));
  const hours = Math.floor((timeValue - weeks * 60 * 24 * 7 - days * 24 * 60) / 60);
  const minutes = Math.floor(timeValue - weeks * 60 * 24 * 7 - (timeValue - days * 24 * 60) - hours * 60);
  let ret = '';

  if (days > 0) {
    if (ret !== '') {
      ret += ' ';
    }
    return (ret += days + ' days ');
  }

  if (hours > 0) {
    if (ret !== '') {
      ret += ' ';
    }
    return (ret += hours + ' hours ');
  }

  if (minutes > 0 && includeMinutes) {
    if (ret !== '') {
      ret += ' ';
    }
    return (ret += minutes + ' minutes ');
  }

  return ret + 'ago';
}
